"use client";

import { signIn, signOut, useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import { useState } from "react";

import { Menu, X, LogOut, User, LayoutDashboard, Shield } from "lucide-react";

export default function Navbar() {
  const { data: session } = useSession();
  const [isOpen, setIsOpen] = useState(false);
  const router = useRouter();

  const toggleMenu = () => setIsOpen(!isOpen);
  const closeMenu = () => setIsOpen(false);

  return (
    <nav className="bg-gray-900 text-white shadow-lg sticky top-0 w-full z-50">
      <div className="container mx-auto px-6 py-4 flex justify-between items-center">
        {/* Logo */}
        <div
          className="text-xl font-bold cursor-pointer hover:text-gray-300 transition"
          onClick={() => router.push("/")}
        >
          MyTool
        </div>

        {/* Menu lớn (Desktop) */}
        <div className="hidden md:flex space-x-6 items-center">
          <NavItem label="Dashboard" icon={LayoutDashboard} href="/dashboard" visible={!!session} />
          <NavItem label="Profile" icon={User} href="/profile" visible={!!session} />
          <NavItem label="Admin Panel" icon={Shield} href="/admin" visible={session?.user?.role === "admin"} />

          {/* Auth buttons */}
          {session ? (
            <button
              onClick={() => signOut()}
              className="flex items-center gap-2 bg-red-500 px-4 py-2 rounded-lg hover:bg-red-600 transition"
            >
              <LogOut size={20} />
              Đăng xuất
            </button>
          ) : (
            <button
              onClick={() => signIn("google")}
              className="bg-blue-500 px-4 py-2 rounded-lg hover:bg-blue-600 transition"
            >
              Đăng nhập
            </button>
          )}
        </div>

        {/* Menu di động (Mobile) */}
        <button className="md:hidden focus:outline-none" onClick={toggleMenu}>
          {isOpen ? <X size={24} /> : <Menu size={24} />}
        </button>
      </div>

      {/* Dropdown Mobile */}
      {isOpen && (
        <div className="md:hidden bg-gray-800 text-white px-6 py-4">
          <NavItem label="Dashboard" icon={LayoutDashboard} href="/dashboard" visible={!!session} closeMenu={closeMenu} />
          <NavItem label="Profile" icon={User} href="/profile" visible={!!session} closeMenu={closeMenu} />
          <NavItem label="Admin Panel" icon={Shield} href="/admin" visible={session?.user?.role === "admin"} closeMenu={closeMenu} />

          {session ? (
            <button
              onClick={() => {
                signOut();
                closeMenu();
              }}
              className="w-full flex items-center gap-2 mt-4 bg-red-500 px-4 py-2 rounded-lg hover:bg-red-600 transition"
            >
              <LogOut size={20} />
              Đăng xuất
            </button>
          ) : (
            <button
              onClick={() => {
                signIn("google");
                closeMenu();
              }}
              className="w-full mt-4 bg-blue-500 px-4 py-2 rounded-lg hover:bg-blue-600 transition"
            >
              Đăng nhập
            </button>
          )}
        </div>
      )}
    </nav>
  );
}

// Component link menu
function NavItem({ label, icon: Icon, href, visible, closeMenu }) {
  if (!visible) return null;

  return (
    <a
      href={href}
      className="flex items-center gap-2 px-4 py-2 hover:bg-gray-700 rounded-lg transition w-full md:w-auto"
      onClick={closeMenu}
    >
      <Icon size={20} />
      {label}
    </a>
  );
}
